import React, { useContext, useState } from 'react'
import { LoginContext } from '../Context/LogIn'
import { notification } from 'antd';
import {ExclamationCircleTwoTone} from '@ant-design/icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'

const username = 'admin';
const password = 'admin';

function LogInPage() {
  let loginState = useContext(LoginContext);
  let [user, setUser] = useState('');
  let [pass, setPass] = useState('');
  let [api, ContextHandler] = notification.useNotification();
  function VerifyDetails() {
    if (user === username && pass === password) {
      loginState.setLoggedIn(true);
      setUser('');
      setPass('');
    }
    else {
      if(user === '' || pass === '')
      {
        notification.info({
          message: <span style={{ color: '#66FCF1' }}>Error</span>,
          description: <span style={{ color: 'white' }}>Please fill all the fields</span>,
          icon: <ExclamationCircleTwoTone twoToneColor="red" />,
          type: 'error',
          placement: 'top',
          style: { backgroundColor: '#1F2833', color: 'white' }
        });
      }
      else
      {
        notification.info({
          message: <span style={{ color: '#66FCF1' }}>Error</span>,
          description: <span style={{ color: 'white' }}>Your username or password is incorrect</span>,
          icon: <ExclamationCircleTwoTone twoToneColor="red" />,
          type: 'error',
          placement: 'top',
          style: { backgroundColor: '#1F2833', color: 'white' }
        });
      }
    }
  }
  return (
    <div>
      <div className="loginBox">
        {ContextHandler}
        <h1>Sign Up</h1>
        <div className='loginContent'>
          <div class="contactUsDivPart">
            <input onChange={(e) => setUser(e.target.value)} type="text" placeholder="" />
            <label for="email">Email</label>
          </div>

          <div class="contactUsDivPart">
            <input onChange={(e) => setPass(e.target.value)} type="password" placeholder="" />
            <label for="password">Password</label>
          </div>
        </div>
        <button style={{marginLeft:'5%',marginRight:'5%',marginTop:'20px',fontSize:'1.3rem'}} className='btn btn-outline-primary' onClick={VerifyDetails}>Login</button>
        <button style={{marginLeft:'5%',width:'20%',marginBottom:'5%',marginTop:'20px',fontSize:'1.3rem'}}  className='btn btn-outline-primary' onClick={VerifyDetails}>Register</button>
        <div className="socialMediaIcons">
        <FontAwesomeIcon icon="fa-brands fa-google" />
        </div>
      </div>
    </div>
  )
}

export default LogInPage