import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion';
import {CheckCircleTwoTone} from '@ant-design/icons';
import {notification} from 'antd';
function OurTeam() {

  let [api,contextHolder] = notification.useNotification();
  let ourTeamText = 'Lorem, ipsum dolor sit amet consectetur adipisicing elit. Exercitationem dolores libero officia, fugiat consectetur repellendus. Facere consectetur ducimus laudantium aliquam placeat provident voluptate, veniam corrupti aperiam quos alias omnis delectus, tempore laboriosam excepturi asperiores necessitatibus, reiciendis sunt repudiandae adipisci'.split(" ");
  let [makeVisible, setMakeVisible] = useState();
  useEffect(() => {
    setTimeout(() => { setMakeVisible(!makeVisible) }, 300);
  },[]);

  const showNotifications = () => {
    notification.info({
      message: <span style={{color:'#66FCF1'}}>Success</span>,
      description: <span style={{color:'white'}}>Your request has been successfully sent.</span>,
      icon:<CheckCircleTwoTone twoToneColor="#66FCF1"/>,
      type:'success',
      style:{backgroundColor:'#1F2833',color:'white'}
    });  
  };

  return (
    <div className='boxShadow OurTeamBox'>
      <h1 className='gradient-colors'>Our Team</h1>
      <div className='OurTeamBoxContent'>
        <div className={`OurTeamBoxImage ${makeVisible ? 'left-to-right-show' : 'left-to-right-hidden'}`}>
          <h3 style={{ textAlign: 'center' }}>Our Leader</h3>
          <img src={require('E:/Web Developement/development_website/src/Images/ZeeteeOurTeam.png')} alt="" />
        </div>
        <div className={`OurTeamBoxText ${makeVisible ? 'down-to-up-show' : 'down-to-up-hidden'}`}>
          <div className="contactUsDiv">
            <h1 className='gradient-colors'>Contact Us</h1>
            <form action="">
              {contextHolder}
              <div class="contactUsDivPart">
                <input type="text" id="name" placeholder=" " />
                <label for="name">Name</label>
              </div>

              <div class="contactUsDivPart">
                <input type="email" id="email" placeholder=" " />
                <label for="email">Email</label>
              </div>

              <div class="contactUsDivPart">
                <input type="tel" maxLength={10} id="email" placeholder=" " />
                <label for="email">Phone</label>
              </div>
              <div className='contactUsDivPart'>
                <label htmlFor=""></label>
                <textarea placeholder='Discuss With Us' name="" id=""></textarea>
              </div>
              <div>
                <button className='btn btn-primary' onClick={(e)=>{e.preventDefault(); showNotifications()}}>submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}

export default OurTeam